
.about-page {
    text-align: center;
    font-family: "Arial", sans-serif;
    background-color: #e9a3be;
    color: #333;
    padding-bottom: 50px;
}


.about-header {
    background: linear-gradient(to right, #aa375d, #ae2c53);
    color: white;
    padding: 60px 20px;
}

.about-header h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
}

.about-header p {
    font-size: 1.2rem;
    opacity: 0.9;
}


.about-content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap;
    margin: 50px auto;
    max-width: 1200px;
}


.about-card {
    flex: 1;
    max-width: 350px;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease;
}

.about-card:hover {
    transform: translateY(-5px);
}


.icon {
    font-size: 2.5rem;
    color: #007bff;
    margin-bottom: 15px;
}


.about-image {
    flex: 1;
}

.about-image img {
    width: 100%;
    max-width: 350px;
    border-radius: 10px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3);
}

.team-section {
    margin-top: 60px;
    padding: 40px 20px;
    background-color: rgb(241, 240, 245);
}

.team-section h2 {
    font-size: 2rem;
    margin-bottom: 20px;
}

.team-container {
    display: flex;
    justify-content: center;
    gap: 30px;
    flex-wrap: wrap;
}

.team-member {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease;
    max-width: 250px;
}

.team-member:hover {
    transform: scale(1.05);
}

.team-icon {
    font-size: 2rem;
    color: #ff5733;
    margin-bottom: 10px;
}

@media screen and (max-width: 900px) {
    .about-content {
        flex-direction: column;
        text-align: center;
    }

    .team-container {
        flex-direction: column;
        align-items: center;
    }
}
