/* Contact.css */

.contact-section {
  padding: 80px 20px;
  font-family: 'Poppins', sans-serif;
  background: #f0f4f8;
  color: #333;
}

.contact-title {
  font-size: 42px;
  text-align: center;
  margin-bottom: 10px;
  color: #222;
  font-weight: 600;
}

.contact-description {
  text-align: center;
  font-size: 18px;
  margin-bottom: 50px;
  color: #555;
}

.contact-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
}

.contact-info,
.contact-form-container {
  flex: 1;
  min-width: 320px;
  max-width: 500px;
}

.contact-card {
  background: #fff;
  padding: 25px;
  border-radius: 20px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
  text-align: center;
  transition: transform 0.3s ease;
}

.contact-card:hover {
  transform: translateY(-5px);
}

.contact-icon {
  font-size: 36px;
  margin-bottom: 10px;
  color: #7e57c2;
}

.contact-card h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 5px;
}

.contact-card p {
  font-size: 16px;
  color: #666;
}

.contact-form {
  background: #fff;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
}

.input-group {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
  flex-wrap: wrap;
}

.contact-input,
.contact-textarea {
  width: 100%;
  padding: 15px;
  border-radius: 12px;
  border: 1px solid #ddd;
  font-size: 16px;
  color: #333;
  background: #fdfdfd;
  transition: border 0.3s ease;
}

.contact-input:focus,
.contact-textarea:focus {
  border: 1px solid #7e57c2;
  outline: none;
}

.contact-textarea {
  height: 120px;
  resize: none;
}

.contact-button {
  width: 100%;
  background: #7e57c2;
  color: #fff;
  padding: 15px;
  font-size: 18px;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.contact-button:hover {
  background: #5e35b1;
  transform: scale(1.03);
}

.status-message {
  margin-top: 15px;
  padding: 12px;
  border-radius: 10px;
  font-size: 16px;
  text-align: center;
}

.status-success {
  background-color: #e0f7e9;
  color: #2e7d32;
}

.status-error {
  background-color: #fdecea;
  color: #c62828;
}

.social-links {
  margin-top: 60px;
  text-align: center;
}

.social-links h3 {
  font-size: 24px;
  margin-bottom: 15px;
  color: #444;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.social-icon {
  font-size: 28px;
  color: #7e57c2;
  transition: color 0.3s, transform 0.3s;
}

.social-icon:hover {
  color: #5e35b1;
  transform: translateY(-3px);
}

.popup {
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  background: #ffffff;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  z-index: 9999;
  animation: fadeIn 0.3s ease-in-out;
}

.popup-content h3 {
  font-size: 22px;
  margin-bottom: 10px;
  color: #2e7d32;
}

.popup-content p {
  font-size: 16px;
  color: #555;
  margin-bottom: 15px;
}

.popup-content button {
  background-color: #7e57c2;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 15px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.popup-content button:hover {
  background-color: #5e35b1;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -10%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

@media (max-width: 768px) {
  .input-group {
    flex-direction: column;
  }
}
.contact-info {
  display: flex;
  flex-direction: column;
  gap: 30px; /* spacing between cards */
}

.contact-card {
  background: #faf4f9;
  padding: 25px;
  border-radius: 20px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
  text-align: center;
  transition: transform 0.3s ease;
}
