
  .container {
    width: 100%;
  }
  

  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 15px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #f208db;
    transition: color 0.3s;
  }
  
  .nav-links a:hover {
    color: #ffa500;
  }
  
  .hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px;
    background: linear-gradient(to right, #696469, #00bfff);
    color: #ffffff;
    width: 94.5%;
    height: 100vh;
  }
  
  .hero-content {
    max-width: 100%;
  }
  
  .hero h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .hero p {
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .cta-button {
    background-color: #f7bb4d;
    border: none;
    padding: 10px 20px;
    font-size: 1.2rem;
    color: #000;
    border-radius: 5px;
    cursor: pointer;
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  
  .cta-button:hover {
    transform: scale(1.05);
    background-color: #ff7700;
  }
  
  .hero-image img {
    width: 90%;
    max-width: 90%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
}
.hero-image {
    position: relative;
    overflow: hidden;
}

.hero-image img {
    transition: transform 0.3s ease, filter 0.3s ease;
}

.hero-image:hover img {
    transform: scale(1.1);
    filter: brightness(0.8);
}
